import * as jwt from "jsonwebtoken";
import * as winston from "winston";
import * as Q from "q";
import {baseRepository} from "./baseRepository";
import {encryption} from "../../util/encryption";
import {baseUser} from "../entity/baseUser";

export abstract class baseUserRepository<T extends baseUser> extends baseRepository<T> {

    update(user: T, id: number): Q.Promise<number> {
        let defer = Q.defer<number>();
        let db = this.getDB(this.domainObject.getDBType());
        if (user.password) {
            user.password = encryption.encryptMd5(user.password);
        }
        db.query(
            "update " + this.domainObject.getTableName() + " set ? where id=?",
            [this.removeID(user), id],
            defer,
            (result) => {
                defer.resolve(result.affectedRows);
            }
        );
        return defer.promise;
    }

    login(username: string, password: string): Q.Promise<any> {
        let encryptedPassword = encryption.encryptMd5(password);
        let defer = Q.defer<any>();
        let pool = this.getDB(this.domainObject.getDBType());
        pool.query(
            "select * from " + this.domainObject.getTableName() + " where username=? and password=?",
            [username, encryptedPassword],
            defer,
            (result) => {
                if (result.length !== 1) {
                    defer.resolve(null);
                } else {
                    this.postProcess(result[0]).then(
                        user => {
                            defer.resolve({id: user.id, token: this.generateJWT(user), detail: user});
                        }
                    );
                }
            }
        );
        return defer.promise;
    }

    changeNewPassword(newpassword: string, userid: any) {
        let defer = Q.defer<any>();
        let pool = this.getDB(this.domainObject.getDBType());
        let psw = encryption.encryptMd5(newpassword);
        pool.query(
            "update " + this.domainObject.getTableName() + " set password=? where id=?",
            [psw, userid],
            defer,
            (result) => {
                defer.resolve(result.affectedRows);
            }
        );
        return defer.promise;
    }

    generateJWT(user: T) {
        winston.debug("generate token for user[id:" + user.id + ",username:" + user.username + ",role:" + user.role + "]");
        let config = global.config;
        return jwt.sign({
            id: user.id,
            username: user.username,
            role: user.role
        }, config.jwtSecKey, {expiresIn: config.jwtTimeout});
    }
}


