import {baseController} from "./baseController";
import {baseRepository} from "../domain/repository/baseRepository";
import {baseDomainObject} from "../domain/entity/baseDomainObject";
import {paginationRequest} from "../domain/pagination/paginationRequest";
import {paginationResponse} from "../domain/pagination/paginationResponse";
import * as Q from "q";

export abstract class baseCRUDController<R extends baseDomainObject, T extends baseRepository<R>> extends baseController {
    repository: T;

    constructor(protected _domainObject, protected _repository) {
        super();
        this.repository = _repository.getInstance();
        //allow override default crud methods
        this.initRouters();
        this.createCRUD();
    }

    //common CRUD for domian objects
    createCRUD() {
        let domainObject = new this._domainObject();
        let RESTUrl = domainObject.getRESTUrl();
        // winston.info("generating CRUD for:" + RESTUrl);
        //get List
        this.get(RESTUrl, (req, res, next) => {
            this.repository.listAll().then(
                (rows) => res.status(200).jsonp(rows),
                (error) => this.handleError(error, res)
            );
        });
        //search
        this.get(RESTUrl + '/search', (req, res, next) => {
            this.repository.search(req.query).then(
                (rows) => res.status(200).jsonp(rows),
                (error) => this.handleError(error, res)
            );
        });

        //search like
        this.get(RESTUrl + '/search/like', (req, res, next) => {
            this.repository.searchLike(req.query).then(
                (rows) => res.status(200).jsonp(rows),
                (error) => this.handleError(error, res)
            );
        });

        this.get(RESTUrl + '/incremental', (req, res, next) => {
            this.repository
                .queryByCreateDate(req.query.startDate, req.query.endDate)
                .then(
                    (result) => res.status(200).jsonp(result),
                    (error) => this.handleError(error, res)
                );
        });

        //get by id
        this.get(RESTUrl + '/:id', (req, res, next) => {
            this.repository.queryByID(req.params.id).then(
                (result) => {
                    if (result) {
                        res.status(200).jsonp(result);
                    } else {
                        res.status(204).jsonp({});
                    }
                },
                (error) => this.handleError(error, res)
            );
        });
        //create
        this.post(RESTUrl, (req, res, next) => {
            if (Array.isArray(req.body)) {
                this.repository.batchInsert(req.body).then(
                    (insertID) => res.status(201).jsonp({affectedRows: insertID}),
                    (error) => this.handleError(error, res)
                );
            } else {
                req.body.create_date = new Date();
                this.repository.insert(req.body)
                    .then(
                        (insertID) => res.status(201).jsonp({id: insertID}),
                        (error) => this.handleError(error, res)
                    );
            }
        });
        //update
        this.put(RESTUrl + '/:id', (req, res, next) => {
            this.repository.update(req.body, req.params.id).then(
                (changedRows) => res.status(200).jsonp({affectedRows: changedRows}),
                (error) => this.handleError(error, res)
            );
        });
        //delete
        this.delete(RESTUrl + '/:id', (req, res, next) => {
            this.repository.delete(req.params.id).then(
                (changedRows) => res.status(200).jsonp({affectedRows: changedRows}),
                (error) => this.handleError(error, res)
            );
        });

        this.post(RESTUrl + '/pagination', (req, res, next) => {
            let paginationRequest = <paginationRequest>req.body;
            let result = new paginationResponse();
            Q.all([
                this.repository.count(),
                this.repository.countAllLike(paginationRequest),
                this.repository.queryAllLike(paginationRequest)
            ])
                .spread(
                    (total, filtered, data) => {
                        result.recordsTotal = total.count;
                        result.recordsFiltered = filtered.count;
                        result.data = data;
                        res.jsonp(result).end();
                    }
                );
        });

    }
}


