import {Subject} from "@reactivex/rxjs";
import * as winston from 'winston';

export class apiService {
    apiSteam = new Subject<api>();

    private static _instance: apiService = new apiService();

    public static getInstance(): apiService {
        return apiService._instance;
    }

    constructor() {
        //TODO check why should winston be re initial here?
        try {
            winston.add(winston.transports.File, {
                filename: 'log/log.lo',
                json: false,
                maxsize: 400000,
                maxFiles: 10,
                level: 'debug'
            });
            winston.remove(winston.transports.Console);
            winston.add(winston.transports.Console, {
                prettyPrint: true,
                colorize: true,
                silent: false,
                timestamp: function () {
                    return new Date().toLocaleString();
                },
                level: 'debug'
            });
        } catch (e) {
            console.log(`a winston init err?${e}`);
        }
        winston.info("init api service");
        if (apiService._instance) {
            throw new Error("Error: Instantiation failed: Use getInstance() instead of new.");
        }
        apiService._instance = this;

        this.apiSteam.subscribe(
            info => {
                winston.debug(`creating api on URL:{${info.method}}     ${info.url}`);
            }
        );
    }

    push(info: api) {
        this.apiSteam.next(info);
    }

}

export interface api {
    method: string;
    url: string;
}


