import * as express from "express";
import * as jwt from "jsonwebtoken";
import * as winston from "winston";
import * as Q from "q";
import {serviceException} from "../exception/serviceException";
import {apiService} from "../util/apiService";

export abstract class baseController {
    router: express.Router;

    constructor() {
        this.router = express.Router();
    }

    //abstract methods
    abstract initRouters();

    public getRouter() {
        return this.router;
    }

    //authencation part
    checkAuthentication(req, res, next) {
        let auth = req.get('jwt');
        if (!auth) {
            winston.error('request without token, reject.');
            this.handleError(new serviceException("ERR_nologin"), res);
        } else {
            let config = global.config;
            jwt.verify(auth, config.jwtSecKey, (err, decoded) => {
                if (err) {
                    winston.error(err.message);
                    this.handleError(new serviceException("ERR_relogin", err.message), res);
                } else {
                    winston.debug("user[" + JSON.stringify(decoded) + "] check token success");
                    next();
                }
            });
        }
    }

    getAuthentication(req): Q.Promise<any> {
        let defer = Q.defer<any>();
        let auth = req.get('jwt');
        if (!auth) {
            defer.reject(new serviceException("ERR_relogin"));
        } else {
            let config = global.config;
            jwt.verify(auth, config.jwtSecKey, (err, decoded) => {
                if (err) {
                    defer.reject(new serviceException("ERR_relogin"));
                } else {
                    winston.debug("user from token:[" + JSON.stringify(decoded) + "]");
                    defer.resolve(decoded);
                }
            });
        }
        return defer.promise;
    }

    getAuthenticationSync(req) {
        try {
            let auth = req.get('jwt');
            let config = global.config;
            var decoded = jwt.verify(auth, config.jwtSecKey);
            return decoded;
        } catch (err) {
            return null;
        }
    }

    //common get post del put etc.
    get(url: string, callback: (req, res, next) => void, withAuth: boolean = true) {
        apiService.getInstance().push({method: 'get', url: url});
        if (withAuth) {
            this.router.get(url, this.checkAuthentication.bind(this), callback);
        } else {
            this.router.get(url, callback);
        }
    }

    post(url: string, callback: (req, res, next) => void, withAuth: boolean = true) {
        apiService.getInstance().push({method: 'post', url: url});
        if (withAuth) {
            this.router.post(url, this.checkAuthentication.bind(this), callback);
        } else {
            this.router.post(url, callback);
        }
    }

    put(url: string, callback: (req, res, next) => void) {
        apiService.getInstance().push({method: 'put', url: url});
        this.router.put(url, this.checkAuthentication.bind(this), callback);
    }

    delete(url: string, callback: (req, res, next) => void) {
        apiService.getInstance().push({method: 'delete', url: url});
        this.router.delete(url, this.checkAuthentication.bind(this), callback);
    }

    //utils
    handleError(error: any, res, req?) {
        try {
            res.status(error.statusCode).jsonp(error);
            winston.error(`server error:${JSON.stringify(error)}`);
        } catch (e) {
            winston.error(`unknown server error:${error.stack}`);
            res.status(500).jsonp({
                "errCode": "ERR_500",
                "message": "Server error",
                "statusCode": 500,
                "error": "unknown Server error"
            });
        }
    }

}


