import * as pg from "pg";
import * as winston from "winston";
import * as Q from "q";
import {serviceException} from "../../exception/serviceException";
import {baseDB} from "../baseDB";
export abstract class PostgreSQLDB implements baseDB {
    createPool(cfg, name): pg.Pool {
        let pool = new pg.Pool({
            max: cfg.connectionLimit,
            host: cfg.host,
            database: cfg.database,
            user: cfg.user,
            password: cfg.password
        });
        winston.debug("PostgreSQL database(" + name + ") at:" + cfg.host + " pool created.");
        return pool;
    }

    abstract getPool(param: any): pg.Pool;

    abstract init();

    public query(sql: string, param: any, defer: Q.Deferred<any>, callback: (rows: any) => void, meid?: string) {
        winston.debug('execute sql:' + sql);
        winston.debug('with param:' + JSON.stringify(param));
        this.getPool(meid).query(sql, param, (err, rows) => {
            if (err) {
                winston.error("while execute SQL: " + sql);
                winston.error("with params: " + JSON.stringify(param));
                winston.error(err.toString());
                defer.reject(new serviceException("ERR_DB", err.toString()));
            } else {
                if (callback) {
                    callback(rows);
                }
            }
        });
    }
}


