import * as Q from "q";
import {baseDomainObject} from "../entity/baseDomainObject";
import {dbType, baseDB} from "../../db/baseDB";
import {paginationRequest} from "../pagination/paginationRequest";
import {getExportable, getSubQuery} from "../decorator/RESTEntity";
import {centralizedMySQL} from "../../db/MySQL/centralizedMySQL";
import {distributedMySQL} from "../../db/MySQL/distributedMySQL";
export abstract class baseRepository<T extends baseDomainObject> {
    domainObject: T;
    centralizedDB: centralizedMySQL = centralizedMySQL.getInstance();
    distributedDB: distributedMySQL = distributedMySQL.getInstance();

    constructor(protected _domainObject) {
        this.domainObject = new this._domainObject();
    }

    getDomainObject(): T {
        return this.domainObject;
    }

    insert(objectToInsert: T, indicator?: string): Q.Promise<number> {
        let defer = Q.defer<number>();
        let db = this.getDB(this.domainObject.getDBType());
        db.query(
            "insert into " + this.domainObject.getTableName() + " set ?",
            objectToInsert,
            defer,
            (rows) => {
                defer.resolve(rows.insertId);
            }, indicator);
        return defer.promise;
    }

    batchInsert(objectsToInsert: T[], indicator?: string): Q.Promise<number> {
        let defer = Q.defer<number>();
        let db = this.getDB(this.domainObject.getDBType());
        let keys = Object.keys(objectsToInsert[0]);
        let data = [];
        objectsToInsert.forEach(
            (domainObject) => {
                let values = [];
                keys.map((key => {
                    values.push(domainObject[key]);
                }));
                data.push(values);
            }
        );
        db.query(
            "insert into " + this.domainObject.getTableName() + " (" + keys.join() + ") values ?",
            [data],
            defer,
            (rows) => {
                defer.resolve(rows.affectedRows);
            }, indicator);
        return defer.promise;
    }

    queryByID(id: number, postProcess: boolean = true, indicator?: string): Q.Promise<T> {
        let defer = Q.defer<T>();
        let db = this.getDB(this.domainObject.getDBType());
        db.query(
            "select * from " + this.domainObject.getTableName() + " where id=?",
            [id],
            defer,
            (rows) => {
                if (rows.length !== 1) {
                    defer.resolve(null);
                } else {
                    if (postProcess) {
                        this.postProcess(<T>rows[0]).then(defer.resolve);
                    } else {
                        defer.resolve(<T>rows[0]);
                    }
                }
            }, indicator
        );
        return defer.promise;
    }

    listAll(postProcess: boolean = true, indicator?: string): Q.Promise<Array<T>> {
        let defer = Q.defer<Array<T>>();
        let db = this.getDB(this.domainObject.getDBType());
        db.query(
            "select * from " + this.domainObject.getTableName() + " order by id desc ",
            [],
            defer,
            (rows) => {
                if (postProcess) {
                    Q.all<T>(rows.map(this.postProcess.bind(this))).then(values => {
                        defer.resolve(rows);
                    });
                } else {
                    defer.resolve(rows);
                }
            }, indicator
        );
        return defer.promise;
    }

    update(objectToUpdate: any, id: number, indicator?: string): Q.Promise<number> {
        let defer = Q.defer<number>();
        let db = this.getDB(this.domainObject.getDBType());
        db.query(
            "update " + this.domainObject.getTableName() + " set ? where id=?",
            [this.removeID(objectToUpdate), id],
            defer,
            (result) => {
                defer.resolve(result.affectedRows);
            }, indicator
        );
        return defer.promise;
    }


    updateAndRefresh(objectToUpdate: any, id: number, indicator?: string): Q.Promise<T> {
        return this.update(objectToUpdate, id, indicator).then(
            () => {
                return this.queryByID(id, true, indicator);
            }
        );
    }


    delete(id: number, indicator?: string): Q.Promise<number> {
        let defer = Q.defer<number>();
        let db = this.getDB(this.domainObject.getDBType());
        db.query(
            "delete from " + this.domainObject.getTableName() + " where id=?",
            [id],
            defer,
            (result) => {
                defer.resolve(result.affectedRows);
            }, indicator
        );
        return defer.promise;
    }

    batchDelete(query, indicator?: string): Q.Promise<number> {
        let defer = Q.defer<number>();
        let where = " ";
        let first = true;
        let arg: Array<any> = [];
        Object.keys(query).forEach((key) => {
            if (first) {
                where += " where " + key + this.getConditionKey(query[key]);
                first = false;
            } else {
                where += " and " + key + this.getConditionKey(query[key]);
            }
            Array.prototype.push.apply(arg, this.getConditionArg(query[key]));
        });
        let db = this.getDB(this.domainObject.getDBType());
        db.query(
            "delete from " + this.domainObject.getTableName() + where,
            arg,
            defer,
            (result) => {
                defer.resolve(result.affectedRows);
            }, indicator
        );
        return defer.promise;
    }

    search(query, indicator?: string): Q.Promise<Array<T>> {
        let defer = Q.defer<Array<T>>();
        let where = " ";
        let first = true;
        let arg: Array<any> = [];
        Object.keys(query).forEach((key) => {
            if (first) {
                where += " where " + key + this.getConditionKey(query[key]);
                first = false;
            } else {
                where += " and " + key + this.getConditionKey(query[key]);
            }
            Array.prototype.push.apply(arg, this.getConditionArg(query[key]));
        });
        let db = this.getDB(this.domainObject.getDBType());
        db.query(
            "select * from " + this.domainObject.getTableName() + where + " order by id desc ",
            arg,
            defer,
            (rows) => {
                Q.all<T>(rows.map(this.postProcess.bind(this))).then(() => {
                    defer.resolve(rows);
                });
            }, indicator
        );
        return defer.promise;
    }

    searchLike(query, indicator?: string): Q.Promise<Array<T>> {
        let defer = Q.defer<Array<T>>();
        let where = " ";
        let first = true;
        let arg: Array<any> = [];
        Object.keys(query).forEach((key) => {
            if (first) {
                where += " where " + key + " like ? ";
                first = false;
            } else {
                where += " and " + key + " like ? ";
            }
            arg.push('%' + query[key] + '%');
        });
        let db = this.getDB(this.domainObject.getDBType());
        db.query(
            "select * from " + this.domainObject.getTableName() + where + " order by id desc ",
            arg,
            defer,
            (rows) => {
                Q.all<T>(rows.map(this.postProcess.bind(this))).then(() => {
                    defer.resolve(rows);
                });
            }, indicator
        );
        return defer.promise;
    }

    count(indicator?: string): Q.Promise<number> {
        let defer = Q.defer<number>();
        let db = this.getDB(this.domainObject.getDBType());
        db.query(
            "select count(1) as count from " + this.domainObject.getTableName(),
            [],
            defer,
            (result) => {
                defer.resolve(result[0]);
            }, indicator
        );
        return defer.promise;
    }

    countAllLike(paginationRequest: paginationRequest, indicator?: string) {
        let defer = Q.defer<any>();
        let db = this.getDB(this.domainObject.getDBType());

        let arg: Array<any> = [];
        let where = "";

        //search all columns
        if (paginationRequest.searchAllColumn && paginationRequest.searchAllColumn.trim().length > 0) {
            where = " where (";
            let first = true;
            for (let column of paginationRequest.columns) {
                if (first) {
                    first = false;
                    where += column.name + " like ?";
                } else {
                    where += " or " + column.name + " like ?";
                }
                arg.push("%" + paginationRequest.searchAllColumn + "%");
            }
            where += ") ";
        }

        db.query(
            "select count(1) as count from " + this.domainObject.getTableName() + where,
            arg,
            defer,
            (result) => {
                defer.resolve(result[0]);
            }, indicator
        );
        return defer.promise;
    }

    queryAllLike(paginationRequest: paginationRequest, indicator?: string) {
        let defer = Q.defer<any>();
        let db = this.getDB(this.domainObject.getDBType());

        let arg: Array<any> = [];
        let where = "";

        //search all columns
        if (paginationRequest.searchAllColumn && paginationRequest.searchAllColumn.trim().length > 0) {
            where = " where (";
            let first = true;
            for (let column of paginationRequest.columns) {
                if (first) {
                    first = false;
                    where += column.name + " like ?";
                } else {
                    where += " or " + column.name + " like ?";
                }
                arg.push("%" + paginationRequest.searchAllColumn + "%");
            }
            where += ") ";
        }

        arg.push(paginationRequest.start);
        arg.push(paginationRequest.length);
        db.query(
            "select * from " + this.domainObject.getTableName() + where + " order by id desc " + " limit ?,?",
            arg,
            defer,
            (rows) => {
                Q.all<T>(rows.map(this.postProcess.bind(this))).then(() => {
                    defer.resolve(rows);
                });
            }, indicator
        );
        return defer.promise;
    }

    queryByCreateDate(startDate: Date, endDate: Date): Q.Promise<Array<baseDomainObject>> {
        let defer = Q.defer<Array<baseDomainObject>>();
        let pool = this.getDB(this.domainObject.getDBType());
        pool.query(
            "select * from " + this.domainObject.getTableName() + " where create_date between ? and ?" + " order by id desc ",
            [startDate, endDate],
            defer,
            (rows) => {
                defer.resolve(rows);
            }
        );
        return defer.promise;
    }

    //utils functions
    protected getDB(type: dbType): baseDB {
        if (type === dbType.distributed) {
            return this.distributedDB;
        } else if (type === dbType.centralized) {
            return this.centralizedDB;
        }
    }

    //update shall not contains id
    removeID(objectToUpdate: T) {
        if (objectToUpdate.id) {
            delete objectToUpdate.id;
        }
        return objectToUpdate;
    }


    //post process to do something like not exportable column
    postProcess(domain) {
        return Q.all([this.removeNotExportable(domain), this.doSubQuery(domain)]).then(() => domain);
    }

    doSubQuery(domain) {
        let defer = Q.defer();
        let subQuerys = [];
        for (let key in domain) {
            if (getSubQuery(this.domainObject, key)) {
                let handler = getSubQuery(this.domainObject, key);
                if (domain[key]) {
                    subQuerys.push(handler.repository.getInstance().queryByID(domain[key]).then(
                        subObject => {
                            domain[handler.name] = subObject;
                            return domain;
                        }
                    ));
                }
            }
        }
        Q.all(subQuerys).then(() => defer.resolve(domain));
        return defer.promise;
    }

    //process based on decorator
    removeNotExportable(domain) {
        let defer = Q.defer();
        for (let key in domain) {
            if (getExportable(this.domainObject, key) === false) {
                delete domain[key];
            }
        }
        defer.resolve(domain);
        return defer.promise;
    }

    getFields(objectToInsert): Array < string > {
        let result = [];
        Object.getOwnPropertyNames(objectToInsert)
            .forEach((field) => {
                result.push(field);
            });
        return result;
    }

    getConditionKey(query: string): string {
        if (query.toString().indexOf('{') >= 0) {
            let result = " in (";
            let first = true;
            query.substring(query.indexOf('{') + 1, query.length - 1).split(",").forEach(
                () => {
                    if (first) {
                        result += "?";
                        first = false;
                    } else {
                        result += ",?";
                    }
                }
            );
            return result + ") "
        } else {
            return "=? ";
        }
    }

    getConditionArg(query: string): string[] {
        if (query.toString().indexOf('{') == 0) {
            let q = query.substring(query.indexOf('{') + 1, query.length - 1);
            return q.split(",");
        } else {
            return [query];
        }
    }
}


